import axios from "axios";
import {successAlert,errorAlert} from "../alerts.js";

export const signUp = async(username,password) => {
    try{
        const res = await axios.post(`http://127.0.0.1:${process.env.PORT}/users/signup`,{
            username,password
        });
        if(res.data.success === "success"){
            successAlert("success","Uurla qeydiyyatdan kediniz");
            window.setTimeout(() => {
                location.assign("/");
            },1500);
        }
    }
    catch (err){
        errorAlert("error","Xta ba verdi.Yenidn chd edin");
    }
}
export const login = async(username,password) => {
    try{
        const res = await axios.post(`http://127.0.0.1:${process.env.PORT}/users/login`,{
            username,password
        });
        return res;
    }
    catch (err){
        errorAlert("error","stifadi ad v ya ifr shvdir.Yenidn chd edin!");
        window.setTimeout(() => {
            location.assign("/login");
        }, 1500);
    }
}
export const logout = async () => {
    try {
        const res = await axios.post(`http://127.0.0.1:${process.env.PORT}/users/logout`);
        return res;
    } catch (err) {
        errorAlert("error", "Hesabdan xarkn xta ba verdi. Yenidn chd edin!");
    }
};
